import { useState, useEffect, useContext, Suspense, lazy } from "react";
import "./GithubProjectContainer.scss";
import { Button } from "../../shared/button/Button";
import StyleContext from "../../../contexts/StyleContext";
import { githubLink } from "../../../Customization";
import profile from "../../../assets/data/profile.json";

export default function GithubProjectContainer() {
  const GithubRepoCard = lazy(() => import("./githubRepoCard/GithubRepoCard"));
  const FailedLoading = () => null;
  const [repo, setrepo] = useState([]);
  // todo: remove useContex because is not supported
  const { isDark } = useContext(StyleContext);

  useEffect(() => {
    setrepoFunction(profile.data.user.pinnedItems.edges);
  }, []);

  function setrepoFunction(array) {
    setrepo(array);
  }
  if (!(typeof repo === "string" || repo instanceof String)) {
    return (
      <Suspense fallback={<></>}>
        <div className="main" id="opensource">
          <h1 className="project-title">Open Source Projects</h1>
          <div className="repo-cards-div-main">
            {repo.map((v, i) => {
              if (!v) {
                console.error(
                  `Github Object for repository number : ${i} is undefined`
                );
              }
              return (
                <GithubRepoCard repo={v} key={v.node.id} isDark={isDark} />
              );
            })}
          </div>
          <Button
            text={"More Projects"}
            className="project-button"
            href={githubLink}
            openInNewTab={true}
          />
        </div>
      </Suspense>
    );
  } else {
    return <FailedLoading />;
  }
}
